:root{--ebony: #22262A;--darkYellow: #FEDA15;--forestGreen: #19342D;--mistyBlue: #BFD0DA}*{box-sizing:border-box}body,html{font-family:'Open Sans', sans-serif;color:#22262A;color:var(--ebony);background-color:#BFD0DA;background-color:var(--mistyBlue);width:100%;height:auto;display:flex;flex-direction:column;align-items:center;justify-content:center;text-align:center;margin:0;padding:0;overflow-x:hidden;font-weight:200;scroll-behavior:smooth}body{position:relative}h1,h2,h3,h4{font-family:'Playfair Display', serif;color:#19342D;color:var(--forestGreen)}h1{font-size:50px}h2{font-size:35px;margin-bottom:5px}h3{font-size:30px}h4{font-size:20px}p{font-size:18px}header{font-family:'Playfair Display', serif;width:100%;position:absolute;top:0;left:0}.navigation{position:fixed;width:100%;display:flex;flex-direction:row;justify-content:space-between}.navigation .logo{width:70px;display:flex;align-items:center}.navigation .logo img{width:100%;margin:0 15px}.navigation .logo .active{border-top:none}.navigation .hamburger-react{display:none}.navigation ul{height:100%;box-sizing:border-box;list-style-type:none;display:flex;flex-direction:row;flex-wrap:nowrap;justify-content:flex-end;align-items:center;margin:0}.navigation ul div{display:flex;align-items:center}.navigation li{font-weight:300;font-size:25px;padding:0 15px;display:flex;justify-content:center;height:100%}.navigation a{text-decoration:none;color:#22262A;color:var(--ebony);transition:all .1s linear;border-top:0px solid #a2a2a2;height:100%;padding:10px 0}.navigation ul a:hover{border-top:4px solid #a2a2a2}.navigation .active{border-top:4px solid #FEDA15;border-top:4px solid var(--darkYellow)}.blur{position:absolute;-webkit-backdrop-filter:blur(5px);backdrop-filter:blur(5px);width:100%;height:60px;z-index:-1}button{background-color:inherit;border:1px solid #22262A;border:1px solid var(--ebony);color:#22262A;color:var(--ebony);font-family:inherit;padding:15px 32px;text-align:center;text-decoration:none;display:block;width:-webkit-min-content;width:-moz-min-content;width:min-content;font-size:16px;margin:4px 2px;cursor:pointer}button:hover{color:#19342D;color:var(--forestGreen);background-color:#FEDA15;background-color:var(--darkYellow)}.green:hover{background-color:#19342D;background-color:var(--forestGreen);opacity:0.7;color:#BFD0DA;color:var(--mistyBlue)}.podcast-button{border:1px solid #FEDA15;border:1px solid var(--darkYellow)}.hero{height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center}.hero .move{width:100%}.hero p{margin:-10px 0 0 0;text-transform:uppercase}.hero a{text-decoration:none;color:#22262A;color:var(--ebony)}.nacked{border:none;font-size:30px}.nacked:hover{background-color:#BFD0DA;background-color:var(--mistyBlue)}.container{display:flex;flex-direction:row;align-items:center;justify-content:center}.container .description{max-width:700px}#about .container{flex-wrap:wrap-reverse}#about,#projects{padding:100px 30px 20px;display:flex;flex-direction:column;align-items:center;justify-content:flex-start;text-align:center}#about div,#projects div{padding:20px}#about .intro,#projects .intro{line-height:1.7;max-width:500px}#about img{max-width:300px;border-radius:50%}#skills{padding:100px 30px 20px;display:flex;flex-direction:column;align-items:center;justify-content:flex-start;text-align:center}#skills .skills{display:flex;flex-flow:row wrap;justify-content:center;font-size:40px;max-width:500px}#skills .skills div{color:#19342D;color:var(--forestGreen);display:flex;justify-content:center;align-items:center;margin:0 5px}#skills .skills div p{position:absolute;font-weight:400;font-size:20px;opacity:0;max-width:100%;text-align:center}#skills .skills div:hover svg{color:#FEDA15;color:var(--darkYellow);opacity:0.7}#skills .skills div:hover p{opacity:1}#skills div{padding:20px}#skills .intro{line-height:1.7;max-width:500px}#projects img{max-width:500px;border-radius:0px}#projects h3{margin-bottom:10px}#projects h5{margin:5px 0}#projects .buttons{display:flex;flex-direction:row;align-items:center;justify-content:center}#projects .buttons button{margin:0 10px}#projects .buttons a{text-decoration:none;cursor:pointer}#projects .icons{display:flex;flex-direction:row;align-items:center;justify-content:space-evenly;font-size:30px}#projects .icons div{padding:0;color:#19342D;color:var(--forestGreen);display:flex;align-items:center;justify-content:center}#projects .icons div p{position:absolute;font-weight:400;font-size:15px;opacity:0;max-width:100%;text-align:center}#projects .icons div:hover svg{color:#FEDA15;color:var(--darkYellow);opacity:0.7}#projects .icons div:hover p{opacity:1}#contact{padding:100px 30px 50px;display:flex;flex-direction:column;align-items:center}#contact h2{margin-bottom:20px}#contact .contact,#contact .contact form{border:#22262A 1px solid;border:var(--ebony) 1px solid;transition:all .5s ease-in-out;opacity:0;height:0}#contact .contact div,#contact .contact button,#contact .contact form div,#contact .contact form button{display:none}#contact .open,#contact .open form{opacity:1;height:400px;margin-bottom:3rem;transition:all .5s ease-in-out}#contact .open div,#contact .open button,#contact .open form div,#contact .open form button{display:inline;display:initial}#contact form{display:flex;flex-direction:column;align-items:center;width:400px;padding:20px;justify-content:center}#contact form div{padding:5px;font-size:18px;width:95%}#contact form input,#contact form textarea{box-sizing:border-box;background-color:#BFD0DA;background-color:var(--mistyBlue);border:none;border-bottom:1px solid #22262A;border-bottom:1px solid var(--ebony);width:100%;padding:12px 10px;font-family:'Open Sans', sans-serif}#contact form textarea{border-left:1px solid #22262A;border-left:1px solid var(--ebony)}#contact form input:focus,#contact form textarea:focus{border:2px solid #22262A;border:2px solid var(--ebony)}#contact .icons{font-size:60px;margin:20px;display:flex;flex-direction:row}#contact .icons div{padding:0;display:flex;align-items:center;justify-content:center}#contact .icons div a{display:flex;align-items:center;justify-content:center;margin:0 20px;cursor:pointer}#contact .icons div a svg{color:#19342D;color:var(--forestGreen)}#contact .icons div a p{position:absolute;font-weight:400;font-size:20px;opacity:0;max-width:100%;text-align:center}#contact .icons div:hover a svg{color:#FEDA15;color:var(--darkYellow);opacity:0.7}#contact .icons div:hover a p{opacity:1;color:#19342D;color:var(--forestGreen)}iframe{border-radius:2px}iframe .css-cpn8dn{background:red}#podcast{padding:100px 50px;max-width:1200px}#podcast .title{margin-bottom:50px}#podcast img{max-width:300px}#podcast ul{list-style-position:inside;list-style-type:none}#podcast ul li{line-height:1.5}#podcast .odesliFrame{margin-bottom:50px}#podcast .anchorDiv{width:600px;margin:auto}#podcast .anchorDiv .anchorPlayer{text-align:center;height:100px;width:500px;margin:10px}@media only screen and (max-width: 800px){.navigation .hamburger-react{margin:0 15px;display:block}.navigation .hide{display:none}.navigation .blur{-webkit-backdrop-filter:none;backdrop-filter:none}.navigation ul{position:absolute;right:0;margin:50px 0;width:20%;list-style-type:none;display:flex;flex-direction:row;flex-wrap:wrap;justify-content:flex-end;align-items:center;padding-right:15px}.navigation ul li{padding:5px 15px;-webkit-backdrop-filter:blur(8px);backdrop-filter:blur(8px)}.navigation ul a{border-right:0px solid #a2a2a2;padding:0 5px}.navigation ul a:hover{border-top:none;border-right:3px solid #a2a2a2}.navigation ul .active{padding-right:5px;border-top:none;border-right:3px solid #FEDA15;border-right:3px solid var(--darkYellow)}.container{flex-wrap:wrap;max-width:800px}}@media only screen and (max-width: 500px){.hero .move img{width:350px}.hero p{font-size:15px;width:200px;margin-top:10px}#about .container{width:100%}#about .container .intro{width:80%}#skills .skills{width:80%}#projects .container{flex-wrap:wrap}.image{padding-bottom:0}img{width:90%}.description{width:80%;display:flex;flex-direction:column;align-items:center;justify-content:space-evenly}h3{margin-top:-30px}.contact{width:80%}#projects .icons{width:100%}#projects .icons div{margin:0px px}#contact a{margin:0 7px}#contact form{width:100%;margin:0}#contact form input,#contact form textarea{width:70%}#podcast .intro,#podcast h2{width:300px;margin:auto}#podcast .odesliFrame,#podcast .anchorDiv{width:300px;margin:auto}#podcast .odesliFrame .anchorPlayer,#podcast .anchorDiv .anchorPlayer{width:300px;margin:auto}}

